#github-projects .description {
  display: none;
}
#github-projects .links {
  white-space: nowrap;
}
#github-projects td.name {
  padding-left: 30px;
  background: transparent url(data:image/png;base64,) no-repeat 5px 5px;
  background-size: 20px 20px;
}
#github-projects td.docker-image-icon {
  background-image: url(data:image/png;base64,);
}
#github-projects td.cpan-icon {
  background-image: url(data:image/png;base64,);
}
#github-projects td.golang-icon {
  background-image: url(data:image/png;base64,);
}
#github-projects td.ruby-gem-icon {
  background-image: url(data:image/png;base64,);
}
#github-projects td.ansible-plugin-icon {
  background-image: url(data:image/png;base64,);
}
#github-projects td.jquery-plugin-icon {
  background-image: url(data:image/png;base64,);
}
#github-projects td.bash-icon {
  background-image: url(data:image/png;base64,);
}

/* Hide columns on smaller screens */
@media screen and (max-width: 850px) {
  table.list .badges {
    display: none;
  }
}
@media screen and (max-width: 670px) {
  table.list .status,
  table.list .badges {
    display: none;
  }
}
